import React from 'react';
import '../styles/vhs.css';

export default function VHSOverlay({
  children,
  noise = true,
  vibration = false,
  rgbShift = true,
  scanlines = true,
}) {
  const classNames = [
    'vhs-wrapper',
    vibration && 'vhs-vibration',
    rgbShift && 'vhs-rgb-shift',
  ]
    .filter(Boolean)
    .join(' ');

  return (
    <div className="relative">
      {noise && <div className="vhs-noise" />}
      {scanlines && <div className="vhs-scanlines" />}
      <div className={classNames}>{children}</div>
    </div>
  );
}
